import React from 'react';
import { Navbar, Nav, Container, Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';

const NavigationBar = ({ currentUser, logout }) => {
  return (
    <Navbar bg="primary" variant="dark" expand="lg">
      <Container>
        <Navbar.Brand href="/dashboard">StudySync</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link as={Link} to="/dashboard">Dashboard</Nav.Link>
            
            {currentUser.role === 'QUESTION_SETTER' && (
              <>
                <Nav.Link as={Link} to="/questions">Questions</Nav.Link>
                <Nav.Link as={Link} to="/exams">Exams</Nav.Link>
                <Nav.Link as={Link} to="/analytics">Analytics</Nav.Link>
              </>
            )}
            
            {currentUser.role === 'STUDENT' && (
              <>
                <Nav.Link as={Link} to="/exams/active">Take Exams</Nav.Link>
                <Nav.Link as={Link} to="/results">My Results</Nav.Link>
              </>
            )}
            
            <Nav.Link as={Link} to="/forum">Forum</Nav.Link>
          </Nav>
          
          <Nav>
            <Navbar.Text className="me-3">
              Welcome, {currentUser.firstName} {currentUser.lastName}
            </Navbar.Text>
            <Button variant="outline-light" onClick={logout}>
              Logout
            </Button>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
};

export default NavigationBar;
